export default {
  data() {
    return {};
  },
  methods: {
    // 去我的账户管理
    goMineAccountManagement() {
      uni.navigateTo({
        url: '/pages/mineAccountManagement/mineAccountManagement'
      });
    },
    // 点击退出账号 打开弹框
    openBasePopup() {
      this.$refs.popup.open()
    },
    // 关闭弹框
    closeBasePopup() {
      uni.clearStorageSync()
      this.$refs.popup.close()
    }
  }
};
